var Vue = require('vue')
var router = require('vue-router')
var index = require('./app.vue');
var resource = require('vue-resource');
Vue.use(router);
Vue.use(resource);
// app
Vue.http.options.root = 'http://192.168.10.22/website/'; //
Vue.http.options.emulateJSON = true;

window.vue = Vue;
var app = Vue.extend(index)

var router = new router({
  history:false,
  hashbang:false
})

router.map({
    '/index': {
      name: 'index',
      component: function(resolve){
        require(['./views/index.vue'], resolve)
      }
    },
    '/detail/:goodsId': {
      name: 'detail',
      component: function(resolve){  
        require(['./views/detail.vue'], resolve) // 
      }
    },
    '/user': {
      name: 'user',
      component: require('./views/user.vue')
    },
    '/cart': {
      name: 'cart',
      component: require('./views/cart.vue')
    },
    '*': {
      component: require('./views/404.vue')
    }
})
  
///user
router.redirect({
    '/':"/user"
})

//
router.beforeEach(function(transition){
    var toPath = transition.to.path;
    // document.body.scrollTop = 0
    if (toPath.indexOf('detail') > -1) {
        router.app.isShow = false;
    }else{
        router.app.isShow = true;
    }
    //document.body.className = "loading";
    transition.next()
})

//
router.start(app, "app");
window.router = router;

//
router.afterEach(function (transition) {
    //document.body.className = "";
    console.log(': ' + transition.to.path)
})


