<style lang="less">
  @import url(../assets/less/detail.less);
</style>

<template>
  <div class="detail-rule">
    <div class="rule-mark"></div>
    <div class="rule-cont">
      <div class="rule-row clearfix">
        <div class="pic">
          <img src="http://gw.alicdn.com/imgextra/i4/TB1yaqgKpXXXXbDXVXXXXXXXXXX_!!0-item_pic.jpg_720x720q50s150.jpg" alt="">
        </div>
        <p class="price">129.00</p>
        <p>100</p>
        <p></p>
        <span class="rule-close" v-on:click="closeRule()">
          <i class="icon-font icon-del"></i>
        </span>
      </div>
      <div class="rule-row">
        <span class="tit"></span>
        <div class="rule-detail">
          <span class="cur"></span>
        </div>
      </div>
      <div class="rule-row">
        <span class="tit"></span>
        <div class="rule-detail">
          <span>S</span>
          <span>M</span>
          <span>X</span>
          <span>XL</span>
        </div>
      </div>
      <div class="rule-row clearfix">
        <div class="sum">
          <span class="icon-font icon-cut"></span>
          <input class="" type="text" value="1">
          <span class="icon-font icon-plus"></span>
        </div>
        <span class="tit"></span>
      </div>
      <span class="rule-btn"></span>  
    </div>
  </div>
</template>
 
<script>
  module.exports = {
    porp:['dataRule'],
    methods:{
      closeRule:function(){
        var that = this;
        that.$parent.$data.isRule = false;
      }
    }
  }
</script>


